import React from 'react';
import { connect } from 'react-redux';
import ProductCard from '../components/ProductCard/ProductCard';
import Actions from '../redux/actions/Actions';

export const Products = (props) => {
	const styles = {
		productsContainer: {
			display: 'flex',
			flexWrap: 'wrap',
			justifyContent: 'center',
		},
		productTitle: {
			textAlign: 'center',
			margin: '2rem 0',
			color: '#AB8E4E',
		},
	};

	React.useEffect(() => {
		props.getProducts();
	}, []);

	// console.log(props?.getProductData);

	return (
		<div>
			<h1 style={styles.productTitle}>Products</h1>
			<div style={styles.productsContainer}>
				{props.getProductData?.map((zone,i)=>{
					return (
					<ProductCard
					name={zone.name}
					image={zone.image}
					details={zone.details}
					id={zone._id}
					key={i}
					/>
					)
				})}
			</div>
		</div>
	);
};

const mapStateToProps = (state) => ({
	getProductData: state.redu.getProductData,
	getProductMessage: state.redu.getProductMessage,
	getProductLoading: state.redu.getProductLoading,
});

const mapDispatchToProps = (dispatch) => ({
	getProducts: () => dispatch(Actions.getProduct()),
});

export default connect(mapStateToProps, mapDispatchToProps)(Products);
