import React, { useEffect } from 'react';
import Actions from '../redux/actions/Actions';
import { connect } from 'react-redux';
import { useParams } from 'react-router-dom';

export const ProductDetails = (props) => {
	const params = useParams();
	useEffect(() => {
		props.getSingleProduct(params.productId);
	}, []);

	const styles={
		title:{
			color:'#AB8E4E',
			borderBottom:'4px solid #AB8E4E',
			marginBottom:'1rem'
		},
		mainContainer:{
			padding:'3rem',
			display:'flex',
			flexDirection:'column',
			justifyContent:'center',
			alignItems:'center'
		}
	}
	return <div style={styles.mainContainer}>
		<img src={props.getSingleProductData?.image} alt="Chocolate" />
		<h1 style={styles.title}>Name</h1>
		<div>{props.getSingleProductData?.name}</div>
		<h1 style={styles.title}>Details</h1>
		<div>{props.getSingleProductData?.details}</div>
	</div>;
};

const mapStateToProps = (state) => ({
	getSingleProductData: state.redu.getSingleProductData,
	getSingleProductMessage: state.redu.getSingleProductMessage,
	getSingleProductLoading: state.redu.getSingleProductLoading,
});

const mapDispatchToProps = (dispatch) => ({
	getSingleProduct: (data) => dispatch(Actions.getSingleProduct(data)),
});

export default connect(mapStateToProps, mapDispatchToProps)(ProductDetails);
