import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import { Link, NavLink } from 'react-router-dom';
import Actions from '../../redux/actions/Actions';
// Images
import Logo from '../../images/lindtLogo.png';

// Styles
const styles = {
    menuListItems:{
        flexBasis:'33.33%',
        width:'100%',
    },
    menuLeft:{
        textAlign:'left'
    },
    menuCenter:{
        justifySelf:'center',
        display:'flex',
        flexDirection:'row',
        justifyContent:'space-between',
    },
    menuRight:{
        textAlign:'right'
    },
    menuLink:{
        color:'#AB8E4E',
        padding:'1rem'
    },
    menuLinkActive:{
        color:'#AB8E4E',
        border:'1px solid transparent',
        borderTop:'0',
        borderRadius:'50%',
        padding:'1rem',
        backgroundColor:'#d6c8ba'
    }
}

export const WebMenu = (props) => {
    return (
        <>
            <div style={Object.assign({},styles.menuListItems,styles.menuLeft)}><Link to='/home'><img src={Logo} width="150px" alt="LogoLindt" /></Link></div>
            <div style={Object.assign({},styles.menuListItems,styles.menuCenter)}>
                <NavLink style={styles.menuLink} activeStyle={styles.menuLinkActive} to='/home'>Home</NavLink>
                <NavLink style={styles.menuLink} activeStyle={styles.menuLinkActive} to='/products'>Products</NavLink>
                <NavLink style={styles.menuLink} activeStyle={styles.menuLinkActive} to='/about'>About</NavLink>
                <NavLink style={styles.menuLink} activeStyle={styles.menuLinkActive} to='/contact'>Contact Us</NavLink>
            </div>
            {props.isLogged?<div style={Object.assign({},styles.menuListItems,styles.menuRight)}><span style={{paddingRight:'5px'}}>{localStorage.getItem("username")}</span><span onClick={()=>{props.logoutUser()}}>Logout</span></div>:<div style={Object.assign({},styles.menuListItems,styles.menuRight)}><Link to="/login">SignIn</Link></div>}
        </>
    )
}

const mapStateToProps = (state) => ({
    isLogged: state.redu.isLogged
})

const mapDispatchToProps = (dispatch) =>({
    logoutUser: ()=> dispatch(Actions.logoutUser()),
})

export default connect(mapStateToProps, mapDispatchToProps)(WebMenu)
