import { Button, TextField } from '@material-ui/core'
import React, { useState } from 'react'
import { connect } from 'react-redux'
import { Link, useHistory } from 'react-router-dom'
import Actions from '../redux/actions/Actions'

export const Login = (props) => {
    const history=useHistory();
    const [formData, setFormData] = useState({
        username:'',
        password:''
    })

    const handleChange=(data) => (e) => {
        setFormData({...formData,[data]:e.target.value});
        console.log(formData)
    }

    const handleSubmit=()=>{
      if(formData.username==='' || formData.password===''){
        alert('All Fields Are Required!');
      }else{
        props.loginUser(formData,history);
      }
    }

    const styles={
        inputField:{
            width:'100%',
            margin:'1rem 0'
        },
        formContainer:{
            display:'flex',
            flexDirection:'column',
            justifyContent:'center',
            alignItems:'center',
            margin:'0 auto',
            width:'50%'
        },
        button:{
            justifySelf:'start',
            marginTop:'2rem',
            backgroundColor:'#ab8e4e',
            width:'20%'
        }
    }

    return (
        <div style={styles.formContainer}>
            <TextField style={styles.inputField} label="Username" value={formData.username} onChange={handleChange('username')} />
            <TextField style={styles.inputField} label="Password" type="password" value={formData.password} onChange={handleChange('password')} />
            <h4>Dont have an account?<Link to="/register">click here.</Link></h4>
            <Button  style={styles.button} onClick={()=>handleSubmit()}>
                Login
            </Button>
        </div>
    )
}

const mapStateToProps = (state) => ({
    
})

const mapDispatchToProps = (dispatch) =>({
    loginUser: (data,history)=> dispatch(Actions.loginUser(data,history)),
})

export default connect(mapStateToProps, mapDispatchToProps)(Login)
