import { Button, TextField } from '@material-ui/core'
import React, { useState } from 'react'
import { connect } from 'react-redux'
import { useHistory } from 'react-router-dom'
import Actions from '../redux/actions/Actions'


export const Register = (props) => {
    const history = useHistory();
    const [formData, setFormData] = useState({
        username:'',
        password:'',
        firstName:'',
        lastName:''
    })

    const handleChange=(data) => (e) => {
        setFormData({...formData,[data]:e.target.value});
    }

    const handleSubmit=()=>{
      if(formData.username==='' || formData.password==='' || formData.firstName==='' || formData.lastName===''){
        alert('All Fields Are Required!');
      }else{
        props.registerUser(formData,history);
      }
    }

    const styles={
        inputField:{
            width:'100%',
            margin:'1rem 0'
        },
        formContainer:{
            display:'flex',
            flexDirection:'column',
            justifyContent:'center',
            alignItems:'center',
            margin:'0 auto',
            width:'50%'
        },
        button:{
            justifySelf:'start',
            marginTop:'2rem',
            backgroundColor:'#ab8e4e',
            width:'20%'
        }
    }
    return (
        <div style={styles.formContainer}>
            <TextField style={styles.inputField} label="First Name" type="text" value={formData.firstName} onChange={handleChange('firstName')} />
            <TextField style={styles.inputField} label="Last Name" type="text" value={formData.lastName} onChange={handleChange('lastName')} />
            <TextField style={styles.inputField} label="Username" value={formData.username} onChange={handleChange('username')} />
            <TextField style={styles.inputField} label="Password" type="password" value={formData.name} onChange={handleChange('password')} />
            <Button  style={styles.button} onClick={()=>handleSubmit()}>
                Register
            </Button>
        </div>
    )
}

const mapStateToProps = (state) => ({
    
})

const mapDispatchToProps = (dispatch) =>({
    registerUser: (data,history)=> dispatch(Actions.registerUser(data,history)),
})

export default connect(mapStateToProps, mapDispatchToProps)(Register)
