// PRODUCTS
async function getProduct() {
	const url = `${baseURL}/products/all`;
	return await axios
		.get(url)
		.then((response) => response)
		.catch((error) => errorCatch(error));
}

async function getLatestProduct() {
	const url = `${baseURL}/products/all?latest=1`;
	return await axios
		.get(url)
		.then((response) => response)
		.catch((error) => errorCatch(error));
}

async function getSingleProduct(data) {
	const url = `${baseURL}/products/single?product_id=${data}`;
	console.log(url);
	return await axios
		.get(url)
		.then((response) => response)
		.catch((error) => errorCatch(error));
}

export { baseURL, getProduct, getSingleProduct, getLatestProduct,loginUser ,registerUser};
