import { getProduct, getSingleProduct, getLatestProduct, loginUser, registerUser } from '../config';

export const GET_PRODUCT_REQUEST = 'GET_PRODUCT_REQUEST';
export const GET_PRODUCT_SUCCESS = 'GET_PRODUCT_SUCCESS';
export const GET_PRODUCT_ERROR = 'GET_PRODUCT_ERROR';

export const GET_SINGLE_PRODUCT_REQUEST = 'GET_SINGLE_PRODUCT_REQUEST';
export const GET_SINGLE_PRODUCT_SUCCESS = 'GET_SINGLE_PRODUCT_SUCCESS';
export const GET_SINGLE_PRODUCT_ERROR = 'GET_SINGLE_PRODUCT_ERROR';

export const GET_LATEST_PRODUCT_REQUEST= 'GET_LATEST_PRODUCT_REQUEST'
export const GET_LATEST_PRODUCT_SUCCESS= 'GET_LATEST_PRODUCT_SUCCESS'
export const GET_LATEST_PRODUCT_ERROR= 'GET_LATEST_PRODUCT_ERROR'

export const LOGIN_REQUEST= 'LOGIN_REQUEST'
export const LOGIN_SUCCESS= 'LOGIN_SUCCESS'
export const LOGIN_ERROR= 'LOGIN_ERROR'

export const REGISTER_REQUEST= 'REGISTER_REQUEST'
export const REGISTER_SUCCESS= 'REGISTER_SUCCESS'
export const REGISTER_ERROR= 'REGISTER_ERROR'

export const LOGGED_OUT = 'LOGGED_OUT'


const actions = {
	loginUser: (data,history)=> async(dispatch)=>{
		try{
			dispatch({
				type: LOGIN_REQUEST,
			});
			await loginUser(data).then((response) => {
				if (response.status === 200) {
					dispatch({
						type:LOGIN_SUCCESS
					})
					localStorage.setItem("username", response.data.username);
					localStorage.setItem("isLogged", true);
					history.push('/home');
				}
			});
		} catch (error) {
			dispatch({
				type: LOGIN_ERROR,
				payload: {
					message: 'ERROR LOGGING USER',
				},
			});
		}
	},
	registerUser:(data,history)=>async(dispatch)=>{
		try{
			dispatch({
				type: LOGIN_REQUEST,
			});
			await registerUser(data).then((response) => {
				if (response.status === 200) {
					dispatch({
						type:LOGIN_SUCCESS
					})
					localStorage.setItem("username", response.data.username);
					localStorage.setItem("isLogged", true);
					history.push('/home');
				}
			});
		} catch (error) {
			dispatch({
				type: LOGIN_ERROR,
				payload: {
					message: 'ERROR REGISTERING USER',
				},
			});
		}
	},

	logoutUser: ()=> async(dispatch)=>{
		dispatch({
			type: LOGGED_OUT,
		});
		localStorage.removeItem('username');
		localStorage.removeItem('isLogged');
	},

	getProduct: () => async (dispatch) => {
		try {
			dispatch({
				type: GET_PRODUCT_REQUEST,
			});
			await getProduct().then((response) => {
				if (response.status === 200) {
					dispatch({
						type: GET_PRODUCT_SUCCESS,
						payload: {
							data: response.data,
						},
					});
				} else {
					dispatch({
						type: GET_PRODUCT_ERROR,
						payload: {
							message: response.message,
						},
					});
				}
			});
		} catch (error) {
			dispatch({
				type: GET_PRODUCT_ERROR,
				payload: {
					message: 'ERROR GETTING PRODUCTS',
				},
			});
		}
	},

	getLatestProduct: () => async (dispatch) => {
		try {
			dispatch({
				type: GET_LATEST_PRODUCT_REQUEST,
			});
			await getLatestProduct().then((response) => {
				if (response.status === 200) {
					dispatch({
						type: GET_LATEST_PRODUCT_SUCCESS,
						payload: {
							data: response.data,
						},
					});
				} else {
					dispatch({
						type: GET_LATEST_PRODUCT_ERROR,
						payload: {
							message: response.message,
						},
					});
				}
			});
		} catch (error) {
			dispatch({
				type: GET_LATEST_PRODUCT_ERROR,
				payload: {
					message: 'ERROR GETTING PRODUCTS',
				},
			});
		}
	},

	getSingleProduct: (data) => async (dispatch) => {
		try {
			dispatch({
				type: GET_SINGLE_PRODUCT_REQUEST,
			});
			await getSingleProduct(data).then((response) => {
				console.log(response)
				if (response.status === 200) {
					dispatch({
						type: GET_SINGLE_PRODUCT_SUCCESS,
						payload: {
							data: response.data,
						},
					});
				} else {
					dispatch({
						type: GET_SINGLE_PRODUCT_ERROR,
						payload: {
							message: response.message,
						},
					});
				}
			});
		} catch (error) {
			dispatch({
				type: GET_SINGLE_PRODUCT_ERROR,
				payload: {
					message: 'ERROR GETTING SINGLE PRODUCTS',
				},
			});
		}
	},
};

export default actions;
