import React, { useState } from 'react'
import { connect } from 'react-redux'
import { TextField } from '@material-ui/core'
// import { withStyles } from '@material-ui/styles'
import { Button } from '@material-ui/core'

export const Contact = (props) => {
    const [formData, setFormData] = useState({
        name:'',
        email:'',
        message:''
    })

    const handleChange=(data) => (e) => {
        setFormData({...formData,[data]:e.target.value});
        console.log(formData)
    }

    const handleSubmit=()=>{
      if(formData.name==='' || formData.email==='' || formData.message===''){
        alert('All Fields Are Required!');
      }else{
        alert("Message sent successfully, Thank you!");
        setFormData({
          name:'',
          email:'',
          message:''
      })
      }
    }

    // const CssTextField = withStyles({
    //     root: {
    //       '& label.Mui-focused': {
    //         color: '#ab8e4e',
    //       },
    //       '& .MuiInput-underline:after': {
    //         borderBottomColor: '#ab8e4e',
    //       },
    //       '& .MuiOutlinedInput-root': {
    //         '& fieldset': {
    //           borderColor: '#ab8e4e',
    //         },
    //         '&:hover fieldset': {
    //           borderColor: '#ab8e4e',
    //         },
    //         '&.Mui-focused fieldset': {
    //           borderColor: '#ab8e4e',
    //         },
    //       },
    //     },
    //   })(TextField);

      const styles={
          inputField:{
              width:'100%',
              marginBottom:'1rem',
          },
          formContainer:{
              display:'flex',
              flexDirection:'column',
              justifyContent:'center',
              alignItems:'center',
              margin:'4rem 4rem',
          },
          button:{
              justifySelf:'start',
              marginTop:'2rem',
              backgroundColor:'#ab8e4e',
              width:'20%'
          }
      }
    return (
        <div style={styles.formContainer}>
            {/* <CssTextField style={styles.inputField} label="Name" value={formData.name} onChange={handleChange('name')} />
            <CssTextField style={styles.inputField} label="Email" value={formData.email} onChange={handleChange('email')} />
            <CssTextField style={styles.inputField} label="Message" multiline rows={5} value={formData.message} onChange={handleChange('message')} /> */}
            <TextField style={styles.inputField} label="Name" value={formData.name} onChange={handleChange('name')} />
            <TextField style={styles.inputField} label="Email" value={formData.email} onChange={handleChange('email')} />
            <TextField style={styles.inputField} label="Message" multiline rows={5} value={formData.message} onChange={handleChange('message')}/>
            <Button  style={styles.button} onClick={()=>handleSubmit()}>
                Send
            </Button>
        </div>
    )
}

const mapStateToProps = (state) => ({
    
})

const mapDispatchToProps = {
    
}

export default connect(mapStateToProps, mapDispatchToProps)(Contact)
