import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import ProductCard from '../ProductCard/ProductCard'
import Actions from '../../redux/actions/Actions'

export const LatestProducts = (props) => {
    const styles={
        productsContainer:{
            display:'flex',
            flexWrap:'wrap',
            justifyContent:'center',
        },
        productTitle:{
            textAlign:'center',
            margin:'2rem 0',
            color:'#AB8E4E'
        },

    }
    useEffect(() => {
        props.getLatestProducts();
    }, [])
    return (
        <div>
            <h1 style={styles.productTitle}>Latest Products</h1>
            <div style={styles.productsContainer}>
                {props.getLatestProductData?.map((zone,i)=>{
					return (
					<ProductCard
					name={zone.name}
					image={zone.image}
					details={zone.details}
					id={zone._id}
					key={i}
					/>
					)
				})}
            </div>
        </div>
    )
}

const mapStateToProps = (state) => ({
    getLatestProductData: state.redu.getLatestProductData,
	getLatestProductMessage: state.redu.getLatestProductMessage,
	getLatestProductLoading: state.redu.getLatestProductLoading,
})

const mapDispatchToProps = (dispatch) =>({
    getLatestProducts: () => dispatch(Actions.getLatestProduct()),
})

export default connect(mapStateToProps, mapDispatchToProps)(LatestProducts)
