import {
	GET_PRODUCT_REQUEST,
	GET_PRODUCT_SUCCESS,
	GET_PRODUCT_ERROR,
	GET_LATEST_PRODUCT_REQUEST,
	GET_LATEST_PRODUCT_SUCCESS,
	GET_LATEST_PRODUCT_ERROR,
	GET_SINGLE_PRODUCT_REQUEST,
	GET_SINGLE_PRODUCT_SUCCESS,
	GET_SINGLE_PRODUCT_ERROR,
	LOGIN_SUCCESS,
	LOGIN_ERROR,
	LOGGED_OUT
} from '../actions/Actions';

const initialState = {
	isLogged:false,
	getProductData: [],
	getProductMessage: '',
	getProductLoading: false,
	getLatestProductData: [],
	getLatestProductMessage: '',
	getLatestProductLoading: false,
	getSingleProductData: [],
	getSingleProductMessage: '',
	getSingleProductLoading: false,
};

function allActions(state = initialState, action) {
	switch (action.type) {
		case LOGIN_SUCCESS:
			return Object.assign({}, state, {
				isLogged:true
			});
		case LOGIN_ERROR:
			return Object.assign({}, state, {
				isLogged:false
			});
		case LOGGED_OUT:
			return Object.assign({}, state,{
				isLogged:false,
			})
		case GET_PRODUCT_REQUEST:
			return Object.assign({}, state, {
				getProductLoading: true,
			});
		case GET_PRODUCT_SUCCESS:
			return Object.assign({}, state, {
				getProductLoading: false,
				getProductData: action.payload.data,
			});
		case GET_PRODUCT_ERROR:
			return Object.assign({}, state, {
				getProductLoading: false,
				getProductMessage: action.payload.message,
			});
		case GET_LATEST_PRODUCT_REQUEST:
			return Object.assign({}, state, {
				getLatestProductLoading: true,
			});
		case GET_LATEST_PRODUCT_SUCCESS:
			return Object.assign({}, state, {
				getLatestProductLoading: false,
				getLatestProductData: action.payload.data,
			});
		case GET_LATEST_PRODUCT_ERROR:
			return Object.assign({}, state, {
				getLatestProductLoading: false,
				getLatestProductMessage: action.payload.message,
			});
		case GET_SINGLE_PRODUCT_REQUEST:
			return Object.assign({}, state, {
				getSingleProductLoading: true,
			});
		case GET_SINGLE_PRODUCT_SUCCESS:
			return Object.assign({}, state, {
				getSingleProductLoading: false,
				getSingleProductData: action.payload.data,
			});
		case GET_SINGLE_PRODUCT_ERROR:
			return Object.assign({}, state, {
				getSingleProductLoading: false,
				getSingleProductMessage: action.payload.message,
			});

		default:
			return state;
	}
}

export default allActions;
